function BackdropModal({ handleClose, children }) {
  return (
    <motion.div
      onClick={handleClose}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      id='staticModal'
      data-modal-backdrop='static'
      tabindex='-1'
      aria-hidden='true'
      class='bg-[#000000b6]  flex justify-center fixed top-0 left-0 right-0 z-50  w-full p-4 modal-scrollbar  overflow-y-auto overflow-x-hidden md:inset-0 h-screen'>
      <div class='relative w-full h-[fit-content] p-8 max-w-2xl min-w-[360px] '>
        {children}
      </div>
    </motion.div>
  );
}

