import React from 'react'
import AnimatedCursor from 'react-animated-cursor';


function AnimCursor() {
  return (
    <AnimatedCursor
      innerSize={10}
      outerSize={36}
      color='123, 117, 245'
      outerAlpha={0.2}
      innerScale={0.8}
      outerScale={2}
      clickables={[
        'a',
        'input[type="text"]',
        'input[type="email"]',
        'input[type="number"]',
        'input[type="submit"]',
        'input[type="image"]',
        'input[type="svg"]',
        'label[for]',
        'select',
        'textarea',
        'button',
        '.link'
      ]}
    />
  )
}

export default AnimCursor